<template>
    <b-container>
       <b-row>
            <b-col class="text-center">
                <h5>AIIAS Countries Page</h5>
            </b-col>
        </b-row>
        <b-row>
            <b-col class="text-center">
                <b-form-group
                    label="Filter"
                    label-cols-sm="1"
                    label-align-sm="left"
                    label-size="sm"
                    label-for="filterInput"
                >
                    <b-input-group size="sm">
                        <b-form-input
                            v-model="filter"
                            type="search"
                            id="filterInput"
                            placeholder="Type to Search"
                        ></b-form-input>
                
                        <b-input-group-append>
                            <b-button :disabled="!filter" @click="filter = ''">Clear</b-button>
                        </b-input-group-append>
                    </b-input-group>
                </b-form-group>
            </b-col>
        </b-row>
        <b-row>
            <b-col>
                <!-- pagination component  -->
                <b-pagination
                    v-model="currentPage"
                    :total-rows="rows"
                    :per-page="perPage"
                    aria-controls="my-table-meetings"
                    align="left"
                    label="Sort"
                >
                </b-pagination>
            </b-col>
            <b-col class="text-left"> 
                <p>Current Page: {{ currentPage }}</p>
            </b-col>
            <b-col> 
                <div class="text-right">
                    <!-- Pass modal that will be visualized and pass the first component to be focused -->
                    <b-button variant="success"  size="sm" @click="setCountryEdit('0')" v-b-modal.modalEditCountries>
                        Add Country
                    </b-button>
                </div>
            </b-col>
        
        </b-row>
        <b-row>
            <b-col>
                <!-- main table -->
                <b-table
                    class="text-left"
                    id="my-table-country"
                    :items="countries"
                    :fields="fields"
                    :per-page="perPage"
                    :current-page="currentPage"
                    :sort-by.sync="sortBy"
                    :sort-desc.sync="sortDesc"
                    :filter="filter"
                    :filterIncludedFields="filterOn"
                    @filtered="onFiltered"
                    sort-icon-left
                    responsive="sm"
                    small
                >
                    <!-- Buttons -->
                    <template v-slot:cell(actions)="countries">
                        <b-button  variant="primary" class="mr-2" size="sm" @click="setCountryEdit(countries.item.id)"  v-b-modal.modalEditCountries>
                            Edit
                        </b-button>
                        <b-button  variant="danger" class="mr-2" size="sm" v-on:click="deleteData(countries.item.id, countries.item.name)">
                            Delete
                        </b-button>
                    </template>
                </b-table>                   
            </b-col>
        </b-row>
        <b-row>
            <b-col class="modal-footer">
                
            </b-col>
        </b-row>
    
        <ModalEditCountry :country="country" />
    </b-container>
    
</template>


<script>
    import ModalEditCountry from '../components/ModalEditCountry.vue'

    export default {
        name: 'AIIASCountries',
        components: {
            ModalEditCountry
        },
        created() {
            this.fetchCountries()
        },
        data() {
            return {
                filter: null,
                filterOn: [],    
                sortBy: 'Name',
                sortDesc: false,
                fields: [
                    {key: 'name', sortable: true, id: 'id'},
                    {key: 'actions', label: 'Actions'}
                ],
                countries: [],
                
                country: {
                    name: ''
                },
                
                perPage: 10,
                currentPage: 1
            }
        },
        computed: {
            rows() {
                return this.countries.length
            },
            nameState() { 
                return this.country.name.length > 2 ? true : false
            }
        },
        methods: {
            setCountryEdit(country_id) {
                this.country.id = country_id
                this.country.name = ''
            },
            onFiltered(filteredItems) {
                // Trigger pagination to update the number of buttons/pages due to filtering
                this.totalRows = filteredItems.length
                this.currentPage = 1
            },
            //this fetch is necessary to repeat for every table in each file.
            fetchCountries() {
                
                db.collection('country').orderBy('name', 'desc').onSnapshot((querySnapshot) => {
                    let allCountries=[]
                
                    querySnapshot.forEach(doc => {
                        const dbCountry = {
                            id: doc.id,
                            name: doc.data().name
                        }
                        allCountries.push(dbCountry)
                        
                    })
                    this.countries=allCountries
                })
            },
            deleteData(id, name) {
                this.$bvModal.msgBoxConfirm('Are you sure want to delete: ' + name + ' ?')
                .then(value => {
                    if (value) {
                    db.collection('country')
                        .doc(id)
                        .delete()
                        .then(function() {
                            console.log("Document deleted with ID - deleteData(): ", id)
                        })
                        .catch(function(error) {
                            console.error("Error deleting document - deleteData(): ", error)
                        })
                

                    this.$bvModal.msgBoxOk('Document was deleted successfully', {
                        title: 'Confirmation',
                        size: 'sm',
                        buttonSize: 'sm',
                        okVariant: 'success',
                        headerClass: 'p-2 border-bottom-0',
                        footerClass: 'p-2 border-top-0',
                        centered: true
                        })
                        .then(value => {
                            this.boxTwo = value
                        })
                        .catch(err => {
                            // An error occurred
                        })
                    }
                })
                .catch(err => {
                    console.error("Error deleting document - deleteData(): ", err)
                })

            }

        }
    }
</script>
