<template>

    <ValidationObserver ref="observer" v-slot="{ handleSubmit }">
        <b-modal id="modalEditCountries" ref="modalEditCountries" hide-footer title="Edit Country" >
        
            <b-container fluid>
                <!-- <b-form @submit="onSubmit" @reset="onReset"> -->
                <b-form @reset="onReset" @submit.stop.prevent="handleSubmit(onSubmit)">
                    <b-row>
                        <b-col sm="3">
                            Event
                        </b-col>
                        <b-col sm="9">
                            <validation-provider
                                name="Name"
                                :rules="{ required: true, min: 3 }"
                                v-slot="validationContext"
                            >
                                <b-form-input
                                    id="input-country-name"
                                    ref="input-country-name"
                                    name="input-name-country-edit"
                                    v-model="country.name"
                                    placeholder="Name of Country"
                                    maxlength="50"
                                    :state="getValidationState(validationContext)"
                                    aria-describedby="input-1-live-feedback"
                                    trim
                                    autofocus
                                ></b-form-input>
                                <b-form-invalid-feedback id="input-1-live-feedback">{{ validationContext.errors[0] }}</b-form-invalid-feedback>
                            </validation-provider>
                        </b-col>
                    </b-row>
                    <b-row class="row mt-1">
                        <b-col class="modal-footer">
                        </b-col>
                    </b-row>
                    <b-row>
                        <b-col>
                            <b-button variant="success" class="mr-2" type="submit" size="sm" v-if="country.id == '0'">Save changes</b-button>
                            <b-button variant="success" class="mr-2" type="submit" size="sm" v-if="country.id != '0'">Apply changes</b-button>
                            <b-button variant="danger" class="mr-2" type="reset" size="sm" >Reset</b-button>
                        </b-col>
                    </b-row>
                </b-form>
            </b-container>
        
        </b-modal>
    </ValidationObserver>
    
</template>

<script>

    export default {
         
        name: 'ModalEditCountry',
        props: ['country'],
        created() {

                

            this.$root.$on('bv::modal::show', (bvEvent, modalEditCountries) => { 

               if (this.country.id != '0' && typeof this.country.id != 'undefined') {
                db.collection('country').doc(this.country.id)
                        .get()
                        .then(
                            snapshot => {
                                if (!snapshot.exists) return
                                let data = snapshot.data() //
                                this.country.name = data.name
                            }
                        )
               }
                
            })  
        },
        methods: {
            getValidationState({ dirty, validated, valid = null }) {
                return dirty || validated ? valid : null;
            } ,
            onSubmit() {
                if (this.country.id != '0') {
                    let show_country_id = this.country.id
                    let setDoc = 
                        db.collection("country")
                            .doc(this.country.id)
                            .set({
                                date_updated: new Date(),
                                name: this.country.name
                            })
                            .then(function() {
                                console.log("Document written with ID - onSubmit() - SetEvent", show_country_id)
                            })
                            .catch(function(error) {
                                console.error("Error writing document - onSubmit() - SetEvent", error)
                            })
                            
                        this.$bvModal.msgBoxOk('Document was edited successfully', {
                                title: 'Confirmation',
                                size: 'sm',
                                buttonSize: 'sm',
                                okVariant: 'success',
                                headerClass: 'p-2 border-bottom-0',
                                footerClass: 'p-2 border-top-0',
                                centered: true
                                })
                                .then(value => {
                                    this.boxTwo = value
                                })
                                .catch(err => {
                                    // An error occurred
                                })
                }

                if (this.country.id == '0') {
                    db.collection('country').add({
                                date_updated: new Date(),
                                name: this.country.name
                            }
                        )
                        .then(function(docRef) {
                                console.log("Document written with ID - addData(): ", docRef.id)
                            })
                            .catch(function(error) {
                                console.error("Error adding document - addData(): ", error)
                            }); 

                    this.$bvModal.msgBoxOk('Document was added successfully', {
                                title: 'Confirmation',
                                size: 'sm',
                                buttonSize: 'sm',
                                okVariant: 'success',
                                headerClass: 'p-2 border-bottom-0',
                                footerClass: 'p-2 border-top-0',
                                centered: true
                                })
                                .then(value => {
                                    this.boxTwo = value
                                })
                                .catch(err => {
                                    // An error occurred
                                })
                } 
                
                this.$refs['modalEditCountries'].hide()
            },
            onReset(evt){
                evt.preventDefault()

                let focus_object = this.actual_object_focus
                this.country.name = ''
                $(focus_object).focus()
            }
        }
    }
</script>